















































//高德SDK
import amap from '../../common/SDK/amap-wx.js';
export default {

	onLoad() {
		var _this = this;



		this.amapPlugin = new amap.AMapWX({
			//高德地图KEY，随时失效，请务必替换为自己的KEY，参考：http://ask.dcloud.net.cn/article/35070
			key: '5b9b64be2413fc19c26683fcf0de890f'
		});
		//定位地址
		this.amapPlugin.getRegeo({
			success: (data) => {
				//console.log(data)
				this.city = data[0].regeocodeData.addressComponent.city.replace(/市/g, ''); //把"市"去掉

				var lat = data[0].latitude; //纬度
				var lng = data[0].longitude; //经度


				// 自提点列表
				uni.request({
					url: this.config.url + "order/store",
					method: "post",
					data: {
						token: this.token,
						lat: lat,
						lng: lng,

					},
					success: (res) => {
						console.log(res.data)
						if (res.data.code == 1) {
							this.selfList = res.data.data.data
						} else {
                             
						}
					}
				})
			}
		});


	},
	mounted() {



		//我的收获地址
		uni.request({
			url: this.config.url + "member/address",
			method: "post",
			data: {
				token: this.token,

			},
			success: (res) => {
				//console.log(res.data.data, res)
				if (res.data.code == 1) {
					this.homeList = res.data.data
				} else {

				}
			}
		})
	},
	data() {
		return {

			subState: "1",
			typeClass: 'home',
			city: "",
			addr: {},
			selfList: [],
			homeList: [],
			checked: ""
		}
	},
	methods: {
		switchType(type) {

			this.typeClass = type;
			if (type == "home") {
				this.subState = '1';
				console.log(this.subState)
			} else if (type == "self") {
				this.subState = '2';
			}
		},
		add() {
			uni.navigateTo({
				url: "edit/edit?type=add"
			})
		},
		//选取我的收货地址
		selectHome(id) {
			console.log(id)
			this.checked = !this.checked;

		},
		//选取自提点地址
		selectSelf(id,self) {
			var selfAddr=JSON.stringify(self)
			console.log(id,self)
			uni.request({
				url: this.config.url + "station/default",
				method: "post",
				data: {
					token: this.token,
					station_id: id
				},
				success: (res) => {
					
					if (res.data.code == 1) {
						uni.showToast({
							title: '设置成功',
							duration: 1000
						});
						// back不重载
						uni.navigateTo({
							url:"/pages/order/confirmation?self="+selfAddr
						})
						
						// uni.navigateBack({
						// 	url:"/pages/order/confirmation?self="+selfAddr
						// })
					}
				}
			})
		}
	}
}
